import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { sample } from '@ali/4ever-bamboo';
import { eventTracking, TrackingType } from '../actions';

const trackFocusText = sample((controller) => {
  controller.run(
    'onAction',
    eventTracking({
      type: TrackingType.focusText,
    }),
  );
}, { sample: 50 })

export default function onMouseDown(
  event: React.KeyboardEvent,
  controller: Controller,
  next: () => void,
) {
  if (!event.shiftKey) {
    trackFocusText(controller);
  }

  return next();
}
